.contacts {
  padding-top: 44px;
  padding-bottom: 39px;

  overflow: hidden;
}

.contacts__title {
  margin: 0;

  font-size: 18px;
}

.contacts__info {
  width: 100%;
  margin-bottom: 42px;
}

.contacts__picture {
  display: block;
  margin-right: -30px;
  margin-left: -30px;
}

.contacts__image {
  display: block;
  width: 100%;
  margin-bottom: 39px;
}

.contacts__write-us {
  padding: 14px 0;

  font-size: 18px;
  line-height: 20px;
}

@media (min-width: $tablet-width) {
  .contacts {
    padding-top: 72px;
    padding-bottom: 57px;

    background-image: url("../../img/icon-flag.svg");
    background-repeat: no-repeat;
    background-position: 96% 47px;
  }

  .contacts__title {
    font-size: 43px;
  }

  .contacts__info {
    margin-bottom: 61px;
  }

  .contacts__picture {
    display: block;
    margin-right: -50px;
    margin-left: -50px;
  }

  .contacts__image {
    margin-bottom: 55px;
  }

  .contacts__write-us {
    width: 34.43%;
    margin: 0 auto;

    font-size: 17px;
  }
}

@media (min-width: $desktop-width) {
  .contacts {
    position: relative;

    padding-bottom: 105px;

    background-position: 113px 133px;
  }

  .contacts__title {
    margin-top: 81px;
    margin-bottom: 25px;
    padding-right: 50%;
    padding-left: 16.67%;

    font-size: 53px;
  }

  .contacts__info {
    padding-left: 16.67%;
    padding-right: 50%;
  }

  .contacts__picture {
    position: absolute;
    top: 71px;
    right: 50px;

    width: auto;
    margin: 0;
  }

  .contacts__write-us {
    width: 22%;
    margin-top: 84px;
    margin-left: 16.67%;
  }
}
