.form-order__fieldset {
  position: relative;

  width: 100%;
  margin: 0;
  padding: 0;

  border: none;

  &::before {
    content: "";

    position: absolute;
    top: 8px;
    right: 0;

    width: 76.15%;
    height: 1px;

    background-color: $underline-color;
  }

  &--email::before {
    width: 68.85%;
  }
}

.form-order__legend {
  padding: 0;

  font-size: 17px;
  line-height: 16px;
  font-weight: bold;
  text-transform: uppercase;
  color: $main-green;
}

.form-order__list {
  @include list-reset;

  &--choice {
    padding-left: 23.85%;
  }

  &--type {
    padding-top: 43px;
    padding-bottom: 26px;
  }

  &--color {
    padding-top: 44px;
    padding-bottom: 5px;
  }

  &--personification {
    padding-top: 33px;
    padding-bottom: 10px;
  }
}

.form-order__list--personification .form-order__input {
  margin-bottom: 24px;
  padding-top: 18px;
  padding-bottom: 18px;
}

.form-order__item-type {
  margin-bottom: 25px;

  input:checked + label::before,
  input:not(:checked) + label::before {
    content: "";

    position: absolute;
    top: 50%;
    left: -62px;

    width: 40px;
    height: 40px;

    border: solid 2px $greyish-brown;
    border-radius: 50%;

    transform: translateY(-50%);
  }

  input:checked + label::after {
    content: "";

    position: absolute;
    top: 50%;
    left: -50px;

    width: 16px;
    height: 16px;

    background-color: #62d1ba;
    border-radius: 50%;

    transform: translateY(-50%);
  }

  input:checked:not(:disabled):not(:active):hover + label::before,
  input:not(:checked):not(:disabled):not(:active):hover + label::before,
  input:checked:not(:disabled):not(:active):focus + label::before,
  input:not(:checked):not(:disabled):not(:active):focus + label::before {
    border-color: #62d1ba;
  }

  input:not(:checked):not(:disabled):active + label::before {
    border-color: $border-darkgrey;
  }

  input:disabled + label::before {
    opacity: 0.3;
  }

  input:disabled + label {
    opacity: 0.3;
  }
}

.form-order__item-color {
  margin-bottom: 44px;

  input:checked + label::before,
  input:not(:checked) + label::before {
    content: "";

    position: absolute;
    top: 50%;
    left: -60px;

    width: 40px;
    height: 40px;

    border: solid 2px $greyish-brown;

    transform: translateY(-50%);
  }

  input:checked + label::before {
    background-image: url("../../img/icon-tick.svg");
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: 22px 16px;
  }

  input:checked:not(:disabled):not(:active):hover + label::before,
  input:not(:checked):not(:disabled):not(:active):hover + label::before,
  input:checked:not(:disabled):not(:active):focus + label::before,
  input:not(:checked):not(:disabled):not(:active):focus + label::before {
    border-color: #62d1ba;
  }

  input:active:not(:disabled) + label::before {
    border-color: $border-darkgrey;
  }

  input:disabled + label::before {
    opacity: 0.3;
  }

  input:disabled + label {
    opacity: 0.3;
  }
}

.form-order__label {
  position: relative;

  font-size: 17px;
  line-height: 18px;
  font-weight: bold;
  color: $black;

  cursor: pointer;
  user-select: none;
}

.form-order__input {
  width: 100%;
  padding: 0;

  font: inherit;
  font-size: 19px;
  line-height: 18px;
  color: inherit;

  border: none;
  box-shadow: inset 0 -2px 0 0 $black;

  &:hover,
  &:focus,
  &:active {
    box-shadow: inset 0 -2px 0 0 $underline-color;
  }

  &--tel {
    margin-top: 9px;
    margin-bottom: 34px;
    padding-top: 16px;
    padding-bottom: 21px;
  }

  &--email {
    margin-top: 9px;
    margin-bottom: 34px;
    padding-top: 18px;
    padding-bottom: 20px;
  }

  &--textarea {
    margin-top: 23px;
    margin-bottom: 18px;
    padding-top: 18px;
    padding-left: 17px;

    background-color: $white;
    border: solid 2px $greyish-brown;
    box-shadow: none;

    resize: none;

    &:hover,
    &:focus,
    &:active {
      box-shadow: none;
      border: solid 2px $underline-color;
    }
  }

  &::placeholder {   /*доб префиксы для ie11*/
    font: inherit;
    font-size: 17px;
    line-height: 18px;
    color: $placeholder-color;
  }

  &--tel::placeholder { /*доб префиксы для ie11*/
    font-size: 19px;
  }
}

.form-order__note {
  margin-top: 0;
  margin-bottom: 31px;

  font-size: 14px;
  line-height: 16px;
}

@media (min-width: $tablet-width) {
  .form-order__fieldset {
    position: relative;

    padding-left: 25%;

    &::before {
      display: none;
    }

    &--tel {
      margin-top: 0;
    }

    &--email {
      margin-bottom: 0;
    }
  }

  .form-order__fieldset-wrapper {
    margin-right: -50px;
    margin-left: -50px;
    padding-top: 50px;
    padding-right: 50px;
    padding-bottom: 26px;
    padding-left: 50px;

    background-color: $main-grey;

    .form-order__input {
      background-color: $main-grey;
    }
  }

  .form-order__legend {
    position: absolute;
    top: 0;
    left: 0;
  }

  .form-order__list {
    &--choice {
      padding-left: 11.98%;
    }

    &--type {
      padding-top: 0;
      padding-bottom: 9px;
    }

    &--color {
      display: flex;
      flex-wrap: wrap;
      padding-top: 66px;
      padding-bottom: 14px;
      padding-left: 0;
    }

    &--personification {
      padding-top: 50px;
      padding-bottom: 26px;
    }
  }

  .form-order__list--personification .form-order__label {
    padding-right: 5.19%;
  }

  .form-order__item-type {
    margin-bottom: 57px;

    input:checked + label::before,
    input:not(:checked) + label::before {
      left: -80px;

      width: 52px;
      height: 52px;
    }

    input:checked + label::after {
      left: -65px;

      width: 22px;
      height: 22px;
    }
  }

  .form-order__item-color {
    width: 50%;
    margin-bottom: 56px;
    padding-left: 11.98%;

    input:checked + label::before,
    input:not(:checked) + label::before {
      left: -82px;

      width: 54px;
      height: 52px;
    }

    input:checked + label::before {
      background-size: 33px 22px;
    }
  }

  .form-order__item-text {
    display: flex;
    align-items: baseline;
  }

  .form-order__label {
    font-size: 19px;
    line-height: 18px;
  }

  .form-order__input {
    padding-left: 17px;

    &--tel {
      margin-top: 0;
      margin-bottom: 24px;
    }

    &--email {
      margin-top: 0;
      margin-bottom: 24px;
    }

    &--textarea {
      margin-top: 50px;
      margin-bottom: 50px;
    }

    &::placeholder {   /*доб префиксы для ie11*/
      font-size: 19px;
    }
  }

  .form-order__flex-wrapper {
    display: flex;
    flex-direction: row-reverse;
    align-items: baseline;
  }

  .form-order__note {
    margin-bottom: 0;
    margin-left: auto;

    font-size: 17px;
    line-height: 30px;
  }

  .form-order__order {
    width: 34.43%;
  }
}

@media (min-width: $desktop-width) {
  .form-order__fieldset {
    padding-right: 16.67%;
    padding-left: 16.67%;

    &--color {
      padding-right: 14.19%;
    }
  }

  .form-order__fieldset-wrapper {
    margin-right: -50px;
    margin-left: -50px;
    padding-right: 50px;
    padding-left: 50px;
  }

  .form-order__legend {
    position: absolute;
    top: 0;
    left: 6.1%;
  }

  .form-order__list--personification .form-order__label {
    padding-right: 3.72%;
  }

  .form-order__item-color {
    width: 33.3%;
    padding-left: 11.98%;
  }

  .form-order__flex-wrapper {
    padding-right: 16.67%;
    padding-left: 16.67%;
  }
}
