.modal-order {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;

  display: none;
  width: 100%;
  height: 100%;

  background-color: $modal-layer-color;
}

.modal-order__wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  width: 566px;
  max-width: 100%;
  padding-top: 36px;
  padding-right: 30px;
  padding-bottom: 46px;
  padding-left: 30px;

  background-color: $white;
  border: solid 1px #c6c6c6;
  box-shadow: 0 5px 10px 0 rgba(0, 1, 1, 0.25);
}

.modal-order__title {
  margin: 0;
  padding: 0;
  font-size: 25px;
  font-weight: bold;
  color: $black;

  text-align: center;
  text-transform: uppercase;
}

.modal-order__form {
  margin-top: 25px;
}

@media (min-width: $tablet-width) {
  .modal-order__wrapper {
    padding-top: 51px;
    padding-right: 108px;
    padding-left: 108px;
  }

  .modal-order__title {
    font-size: 30px;
    line-height: 57px;

    white-space: nowrap;
  }
}
